import React, { useState, useEffect } from 'react';
import axios from 'axios';
import './css/metadata.css';

function MetadataDisplay() {
  const [metadata, setMetadata] = useState(null);

  useEffect(() => {
    async function fetchMetadata() {
      try {
        // Placeholder data for web server metadata
        const webMetadataData = {
          'Category 1': 'Value 1',
          'Category 2': 'Value 2',
          'Category 3': 'Value 3',
        };

        // Placeholder data for app server metadata
        const appMetadataData = {
          'Category 1': 'Value A',
          'Category 2': 'Value B',
          'Category 3': 'Value C',
        };

        setMetadata({
          web: webMetadataData,
          app: appMetadataData,
        });


        // our original code
        // Step 1: Request a session token for web server
        const webTokenResponse = await axios.put('http://169.254.169.254/latest/api/token', null, {
          headers: {
            'X-aws-ec2-metadata-token-ttl-seconds': 21600
          }
        });
        const webToken = webTokenResponse.data;
        console.log('webtoken: ' + webToken);

        // Step 2: Use the session token to access web server metadata
        const webMetadataResponse = await axios.get('http://169.254.169.254/latest/meta-data/', {
          headers: {
            'X-aws-ec2-metadata-token': webToken
          }
        });
        // setWebMetadata(webMetadataResponse.data);
        console.log('webmetadata-response:' + webMetadataResponse);

        const appResponse = await axios.get('http://localhost:5000/api/app-metadata');
        console.log('app-response:' + appResponse);
        
      } catch (error) {
        console.error('Error fetching metadata:', error);
      }
    }

    fetchMetadata();
  }, []);

  return (
    <div className="metadata-display">
      <h2>EC2 Instance Metadata</h2>
      {metadata ? (
        <table>
          <thead>
            <tr>
              <th>Category</th>
              <th>Web Server</th>
              <th>App Server</th>
            </tr>
          </thead>
          <tbody>
            {Object.keys(metadata.web).map((category) => (
              <tr key={category}>
                <td>{category}</td>
                <td>{metadata.web[category]}</td>
                <td>{metadata.app[category]}</td>
              </tr>
            ))}
          </tbody>
        </table>
      ) : (
        <p className="loading">Loading metadata...</p>
      )}
    </div>
  );
}

export default MetadataDisplay;
