import React, { useState, useEffect } from 'react';
import AWS from 'aws-sdk';

function RDSInformation() {
  const [rdsInstances, setRDSInstances] = useState([]);

  useEffect(() => {
    // Initialize AWS SDK with your credentials
    AWS.config.update({
      accessKeyId: process.env.AWS_ACCESS_KEY_ID,
      secretAccessKey: process.env.AWS_SECRET_ACCESS_KEY,
      region: 'us-east-1',
    });

    // Create an RDS service object
    const rds = new AWS.RDS();

    // Fetch information about RDS instances
    rds.describeDBInstances({}, function(err, data) {
      if (err) {
        console.error('Error fetching RDS instances:', err);
      } else {
        // Extract relevant information from the response
        const instances = data.DBInstances.map(instance => ({
          instanceIdentifier: instance.DBInstanceIdentifier,
          engine: instance.Engine,
          status: instance.DBInstanceStatus,
          // Add more fields as needed
        }));
        setRDSInstances(instances);
      }
    });
  }, []);

  return (
    <div>
      <h2>RDS Instances</h2>
      <ul>
        {rdsInstances.map(instance => (
          <li key={instance.instanceIdentifier}>
            <strong>{instance.instanceIdentifier}</strong> - {instance.engine}, Status: {instance.status}
          </li>
        ))}
      </ul>
    </div>
  );
}

export default RDSInformation;
