import './css/App.css';
import { useEffect, useState } from 'react';
import axios from 'axios';
import image from './images/3tierwebapp.PNG';
import MetadataDisplay from './metadata.js';
import RDSInformation from './dbinfo.js';


function App() {
  const [data, setData] = useState([]);

  useEffect(() => {
    async function fetchData() {
      try {
        const response = await axios.get('http://localhost:5000/api/data');
        setData(response.data);
      } catch (error) {
        console.error('Error fetching data:', error);
      }
    }
    fetchData();
  }, []);

  return (
    <div className="App">
      <header className="App-header">
        <h3>Welcome to AnyCompany website!</h3>
        {/* add image 3tierwebapp.PNG in images folder */}
        <img src={image} height="500px" alt="3-tier web app" />
        <h4>Data from Backend:</h4>
        <ul>
          {data.map(item => (
            <li key={item.id}>{item.name}</li>
          ))}
        </ul>
      <MetadataDisplay />
      <RDSInformation />
      </header>
    </div>
  );
}

export default App;
